import React from "react";
import Layout from "../components/layout";
import Table from "react-bootstrap/Table";
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

export default (props)=>{
    return (
        <Layout>
            <div class="mt-4 px-3 bg-warning border border-primary rounded-lg">
                <Row>
                    <Col>
                        <h3><u>Android APP</u></h3>
                    </Col>
                </Row>
                <Row className="mt-1">
                    <Col lg={3} xs={12}>
                        <img src="https://play-lh.googleusercontent.com/K6gIdI5MkjzJ9NQulaq3fmnH9u32HShGAt2ep_WMZvzGejtMWUH-LKxMODsNzUE_yjQX=s360" 
                                    class="img-thumbnail"
                                    style={{width:180, height:180}}/>
                                <h3 className="text-nowrap">拍車牌 查失車</h3>
                    </Col>
                    <Col className="pl-3 pb-2">
                            <h4>功能及特色:</h4>
                            <ul>
                                <li>可使用 拍照辨識 或 自行輸入 車牌號碼，查詢是否為失竊車牌/車輛</li>
                                <li>介面簡單直覺，不囉嗦; 找到贓車時會發出警示音及震動</li>
                                <li>台灣適用, 資料來源為 內政部資料開放平台 的即時資料</li>
                            </ul>
                            請至Google Play 
                            <small>
                                <a target="_blank"
                                href="https://play.google.com/store/apps/details?id=idv.dolph.missingcar">https://play.google.com/store/apps/details?id=idv.dolph.missingcar</a>
                            </small>
                            或掃描下列QR Code下載安裝!!
                            <p/>
                            <center>
                                <img 
                                class="img-thumbnail" 
                                style={{width:150, height:150}}
                                src="data:image/png;base64,"/>
                            </center>
                    </Col>
                </Row>
            </div>


            <div class="mt-4 px-3 bg-warning border border-primary rounded-lg">
                <Row>
                    <Col>
                        <h3><u>Python Scripts</u></h3>
                    </Col>
                </Row>
                <Row className="mt-1">
                    <Col className="pb-2">
                            <h4 className="text-nowrap">Ebook Extractor</h4>
                            <h5>功能介紹:</h5>
                            <ul>
                                <li>解壓縮RAR或ZIP壓縮裡的.epub及.pdf檔案，並將解壓縮出來的檔案主檔名用原壓縮檔主檔名取代!!</li>
                                <li>適用於許多網路上下載來的電子書壓縮檔!!</li>
                            </ul>
                            
                            使用方式:<br/>
                            <ol>
                                <li>請先確認您的系統有安裝Python3!!</li>
                                <li>請至 <a href="https://bitbucket.org/Dolph_Lei/extractebk/src/master/" target="_blank">https://bitbucket.org/Dolph_Lei/extractebk/src/master/</a> 下載其內三個檔案</li>
                                <li>將您下載來的rar/zip壓縮檔拖曳至extractEbook.bat，即可在壓縮檔同目錄找到解壓縮出來的電子書檔案!!</li>
                            </ol>
                    </Col>
                </Row>
            </div>

            <div class="mt-4 px-3 bg-warning border border-primary rounded-lg">
                <Row>
                    <Col>
                        <h3><u>PaintShop Pro Scripts</u></h3>
                    </Col>
                </Row>
                <Row className="mt-1">
                    <Col className="pb-2">
                            請直接造訪 <a href="https://github.com/dolphlei/dolphs_psp_scripts" target="_blank">https://github.com/dolphlei/dolphs_psp_scripts</a>下載!!<br/>
                            所有Script說明請詳參照該頁README說明!!
                    </Col>
                </Row>
            </div>
        </Layout>
    );
}